@use "../abstracts/variables";
@use "../abstracts/mixins";

.mes-alerts {
  padding: 16px;
  background-color: variables.$color-white;
  @include mixins.boxFlex(center, center, column);

  &.mes-alerts--out-of-stock {
    margin-bottom: 1.5rem;
    padding: 1.5rem;
  }

  .mes-alerts__title {
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-height--general-l;
    color: variables.$color-principal;
    text-align: center;
  }

  .mes-alerts__subtitle {
    margin-bottom: 10px;
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
    text-align: center;
  }

  .mes-alerts__open-btn {
    & > span {
      @include mixins.boxFlex(center, center);
    }

    .alert-blue-icon {
      display: none;
    }

    &:hover {
      .alert-blue-icon {
        display: block;
      }

      .alert-icon {
        display: none;
      }
    }

    &:disabled:hover {
      background-color: variables.$color-principal;
      color: variables.$color-white;

      .alert-blue-icon {
        display: none;
      }

      .alert-icon {
        display: block;
      }
    }
  }
}

.mes-alerts-modal {
  padding: 14px 16px 1.5rem;
  height: auto;

  @include mixins.mediaCustom("min-width: 620px") {
    max-width: 436px;
  }

  @include mixins.media("tablet") {
    padding: 20px 1.5rem 48px;
  }

  .description-modal__close {
    @include mixins.media("tablet") {
      padding-bottom: 17px;
    }
  }

  .description-modal__title {
    margin-bottom: 8px;
  }

  .description-modal__description {
    margin-bottom: 1.5rem;
    padding: 0;
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
    background-color: unset;
  }

  .description-modal__description-text {
    margin-left: 0;
    text-align: center;
  }

  .form__textfield-label > label {
    color: variables.$color-black;
  }

  .form__textfield-label > label {
    margin-bottom: 4px;
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
  }

  .form__textfield-label--spaced {
    margin-bottom: 1rem;

    &:last-of-type {
      margin-bottom: 1.5rem;
    }
  }
  .form__textfield-label.size-s select {
    padding: 12px  10px;
    color: variables.$color-black;
    font-size: variables.$font-size--general-s;
    text-transform: uppercase;
  }

  .btn-1 {
    width: 100%;

    .loading {
      border-top-color: variables.$color-white;
    }

    &:hover .loading {
      border-top-color: variables.$color-principal;
    }

    &:disabled:hover {
      background-color: variables.$color-principal;
      color: variables.$color-white;
    }
  }

  .mes-alerts-modal__btn-login {
    margin-bottom: 1.5rem;
  }

  .mes-alerts-modal__texts-login {
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-s;
    text-align: center;
    @include mixins.boxFlex(center, center, column);

    span {
      margin-bottom: 10px;
      color: variables.$color-black;
    }

    a {
      color: variables.$color-principal;
      font-size: variables.$font-size--general-up-s;
      line-height: variables.$line-height--general-up-s;
      text-transform: uppercase;
    }
  }

  .models-select {
    opacity: .5;
    pointer-events: none;
    transition: opacity .1s ease-out;
  }

  .models-select.models-enable {
    opacity: initial;
    pointer-events: initial;
  }

  .form__textfield-label {
    option[selected] {
      opacity: .5;
    }
  }

  .form__message--error:not(.mes-alerts__response-model-error) {
    margin-top: 1rem
  }

  .mes-alerts__response.form__message {
    border: unset;
  }
}

.mes-alerts__response {
  text-align: center;
  color: variables.$color-principal;
  font-size: variables.$font-size--mobile-h6;
  line-height: variables.$line-height--mobile-h6;
}

#shopify-section-my-wishlist {
  .wk-page {
    padding-top: 0 !important;
  }
  .wk-header {
    display: none;
  }
}