@use '../abstracts/variables';

.container__pagination-product {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  position: relative;

  a {
    text-decoration: none;

    &:hover {
      .show-information {
        display: flex;
        z-index: 100;
      }
    }

    .show-information {
      width: 80%;
      display: none;
      position: absolute;
      justify-content: space-between;
      align-items: center;
      background-color: variables.$color-white;
      padding: 15px;
      box-shadow: 0 0 10px rgb(0 0 0 / .25);
      border-radius: 5px;

      .short-description {
        width: 85%;
        height: auto;
        overflow: hidden;
        text-align: justify;
        margin-left: 15px;

        & > span {
          display: block;
          margin-bottom: 5px;
          font-size: 14px;
          font-weight: 600;
        }

        & > div {
          font-size: 10px;
          font-weight: 400;
        }
      }
    }
  }

  .previous__product {
    .show-information {
      left: 0;
    }
  }

  .next__product {
    .show-information {
      left: 10%;
    }
  }
}
