@use '../theme/theme';
@use '../abstracts/variables';
@use '../abstracts/mixins';

.product-banner {
  position: relative;
  padding: 44px 0;
  margin-top: variables.$margin-section--movil;

  @include mixins.media('tablet') {
    margin-top: variables.$margin-section;
  }
}

.product-banner__content-container {
  width: 100%;
  text-align: center;

  @include mixins.media('tablet-l') {
    @include mixins.mediaCustom('max-width: 1250px') {
      padding-left: variables.$paddings-side-m;
      padding-right: variables.$paddings-side-m;
    }
  }

  .product-banner__title {
    margin-bottom: 8px;
    color: variables.$color-accent-3;
    text-transform: uppercase;
    @extend .h3;
  }
}

.product-banner__content {
  padding: 12px;
  width: 100%;
  background-color: variables.$color-white;

  @include mixins.media('tablet-l') {
    padding: 12px 24px;
  }

  div {
    line-height: variables.$line-height--general-m;
  }
}
